@import './variable.less';
@import './animate.less';
@import './arcgis.less';

body {
  color: @color-text;
  background-color: @color-background;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  -moz-overflow-scrolling: touch;
  -ms-overflow-scrolling: touch;
  -o-overflow-scrolling: touch;
  overflow-scrolling: touch;
}

h1, h2, h3, h4, h5{
  font-size: 16px !important;

}




.page {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 8;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: @color-background;
  &.page-hasFooter {
    bottom: @footer-height;
  }
  &.page-hasTab {
    top: @tab-height;
  }
}
P{
  margin:0;
}



.dateWrap{
  font-size: 16px;
  .iconfont {
    color: @color-theme;
  }
}    

.color-theme {
  color: @color-theme;
}

.color-text {
  color: @color-text;
}

.color-red {
  color: @color-red;
}

.color-warn {
  color: @color-warn;
}

.color-normal {
  color: @color-normal;
}

.color-disabled {
  color: @color-disabled;
}

.color-probStatus-1 {
  color: @color-probStatus-1;
}

.color-probStatus-2 {
