.urgent {
  width: 40px;
  height: 20px;
  line-height: 20px;
  background-color: currentColor;
  padding-left: 8px;
  text-align: center;
  position: absolute;
  margin-left: 10px;
  transform: scale(0.75);
  transform-origin: left top;
  i {
    font-style: normal;
    font-size: 12px;
    color: white;
  }
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: -1px;
    width: 0;
    height: 0;
    overflow: hidden;
    border-width: 10px;
    border-color: transparent transparent transparent white;
    border-style: dashed dashed dashed solid;
  }
}

.form {
  padding: 10px 0;
  font-size:16px;
  .item-group {
    display: flex;
    align-items: center;
    padding: 10px;
    font-size:18px;
    background-color: white;
    h6 {
      margin-right: 1em;
      font-size:18px;
    }
    > div {
      flex: 1;
      text-align: right;
      input {
        width: 100%;
        text-align: right;
      }
    }
    .btnWrap {
      font-size: 0;
    }
    .iconWrap {
      display: flex;
      [class*="hui-icon"] {
        color: @color-theme;
        padding-left: 5px;
        vertical-align: -2px;
      }
    }
    .urgentGrade {
      .radio-item {
        display: inline-block;
        font-size:18px;
        input {
          position: absolute;
          width: 54px;
          height: 28px;
          z-index: 1;
          opacity: 0;

        }
        a{
          font-size:14px;
        }
        #red {
          &:checked + .btn {
            color: white;
            background-color: @color-red;
          }
        }
        #warn {
          &:checked + .btn {
            color: white;
            background-color: @color-warn;
          }
        }
        #normal {
          &:checked + .btn {
            color: white;
            background-color: @color-normal;
          }
        }
      }
    }
  }
  .hiddenTrouble {
    padding: 10px;
    h6 {
      padding-bottom: 5px;
      font-size:18px;
    }
    textarea {
      display: block;
      border: 0;
      resize: none;
      width: 100%;
      height: 110px;
      padding: 10px;
      color: inherit;
      font-size: 18px;
      line-height: inherit;
      outline: 0;
      -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
      box-sizing: border-box;
    }
    .explain {
      text-align: right;
      line-height: 2;
      font-size: 16px;
    }
  }
  .huiUploaderWrap {
    padding: 0 10px;
  }
  .submitWrap {
    margin: 10px;
    .btn {
      width: 100%;
    }
  }
  .dateWrap {
    input {
      position: absolute;
      right: 0;
      width: 100%;
      -webkit-appearance: none;
      opacity: 0;
    }
  }
  .required {
    margin-left: 4px;
  }
  .weui-uploader__title{
    font-size:18px;
  }
  .weui-uploader__input-box{
    width:80px !important;
    height:80px!important;
  }
}

.router-view {
  transition: opacity .4s;
  &.fade-enter,
  &.fade-leave-to {
    opacity: 0;
  }
}

.common-time{
  position: absolute;
  right:10px;
  b{
    color: @color-theme;
  }

}

  .weui-uploader__input{
    opacity: 0 !important;
  }

  .picker-item.picker-selected{
    font-size:28px;
  }

