.animated {
  animation-duration: 0.4s;
  animation-fill-mode: both;
  animation-timing-function: linear;
}
.animated.infinite {
  animation-iteration-count: infinite;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.fadeIn {
  animation-name: fadeIn;
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.fadeOut {
  animation-name: fadeOut;
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.spin {
  animation-name: spin;
}
.esri-view-width-xsmall .esri-popup--is-docked-top-center .esri-popup__main-container,
.esri-view-width-xsmall .esri-popup--is-docked-bottom-center .esri-popup__main-container {
  left: 0;
}
.esri-ui {
  z-index: 10;
}
.popup-table tbody tr td {
  vertical-align: text-top;
}
.popup-table tbody tr td:nth-child(2n) {
  width: 40%;
}
.popup-table tbody tr td:nth-child(2n+1) {
  white-space: nowrap;
  font-weight: bold;
}
.esri-widget-button:active,
.esri-widget-button:focus,
.esri-widget-button:hover {
  background-color: #409EFF !important;
  color: white!important;
}
.esri-ui-top-right .esri-component,
.esri-ui-top-left .esri-component {
  margin-top: 50px;
}
.actionList {
  display: none;
}
.titleButton.maximize {
  display: none;
}
body {
  color: #333333;
  background-color: #eeeff4;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  -moz-overflow-scrolling: touch;
  -ms-overflow-scrolling: touch;
  -o-overflow-scrolling: touch;
  overflow-scrolling: touch;
}
h1,
h2,
h3,
h4,
h5 {
  font-size: 16px !important;
}
.page {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 8;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: #eeeff4;
}
.page.page-hasFooter {
  bottom: 62px;
}
.page.page-hasTab {
  top: 36px;
}
P {
  margin: 0;
}
.dateWrap {
  font-size: 16px;
}
.dateWrap .iconfont {
  color: #409EFF;
}
.color-theme {
  color: #409EFF;
}
.color-text {
  color: #333333;
}
.color-red {
  color: red;
}
.color-warn {
  color: #e6a23c;
}
.color-normal {
  color: #dbc311;
}
.color-disabled {
  color: #a6a6a6;
}
.color-probStatus-1 {
  color: #fe0000;
}
.color-probStatus-2 {
  color: #b72500;
}
.color-probStatus-3 {
  color: #744801;
}
.color-probStatus-4 {
  color: #3f6401;
}
.color-probStatus-5 {
  color: #048301;
}
.urgent {
  width: 40px;
  height: 20px;
  line-height: 20px;
  background-color: currentColor;
  padding-left: 8px;
  text-align: center;
  position: absolute;
  margin-left: 10px;
  transform: scale(0.75);
  transform-origin: left top;
}
.urgent i {
  font-style: normal;
  font-size: 12px;
  color: white;
}
.urgent:before {
  content: '';
  position: absolute;
  top: 0;
  left: -1px;
  width: 0;
  height: 0;
  overflow: hidden;
  border-width: 10px;
  border-color: transparent transparent transparent white;
  border-style: dashed dashed dashed solid;
}
.form {
  padding: 10px 0;
  font-size: 16px;
}
.form .item-group {
  display: flex;
  align-items: center;
  padding: 10px;
  font-size: 18px;
  background-color: white;
}
.form .item-group h6 {
  margin-right: 1em;
  font-size: 18px;
}
.form .item-group > div {
  flex: 1;
  text-align: right;
}
.form .item-group > div input {
  width: 100%;
  text-align: right;
}
.form .item-group .btnWrap {
  font-size: 0;
}
.form .item-group .iconWrap {
  display: flex;
}
.form .item-group .iconWrap [class*="hui-icon"] {
  color: #409EFF;
  padding-left: 5px;
  vertical-align: -2px;
}
.form .item-group .urgentGrade .radio-item {
  display: inline-block;
  font-size: 18px;
}
.form .item-group .urgentGrade .radio-item input {
  position: absolute;
  width: 54px;
  height: 28px;
  z-index: 1;
  opacity: 0;
}
.form .item-group .urgentGrade .radio-item a {
  font-size: 14px;
}
.form .item-group .urgentGrade .radio-item #red:checked + .btn {
  color: white;
  background-color: red;
}
.form .item-group .urgentGrade .radio-item #warn:checked + .btn {
  color: white;
  background-color: #e6a23c;
}
.form .item-group .urgentGrade .radio-item #normal:checked + .btn {
  color: white;
  background-color: #dbc311;
}
.form .hiddenTrouble {
  padding: 10px;
}
.form .hiddenTrouble h6 {
  padding-bottom: 5px;
  font-size: 18px;
}
.form .hiddenTrouble textarea {
  display: block;
  border: 0;
  resize: none;
  width: 100%;
  height: 110px;
  padding: 10px;
  color: inherit;
  font-size: 18px;
  line-height: inherit;
  outline: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  box-sizing: border-box;
}
.form .hiddenTrouble .explain {
  text-align: right;
  line-height: 2;
  font-size: 16px;
}
.form .huiUploaderWrap {
  padding: 0 10px;
}
.form .submitWrap {
  margin: 10px;
}
.form .submitWrap .btn {
  width: 100%;
}
.form .dateWrap input {
  position: absolute;
  right: 0;
  width: 100%;
  -webkit-appearance: none;
  opacity: 0;
}
.form .required {
  margin-left: 4px;
}
.form .weui-uploader__title {
  font-size: 18px;
}
.form .weui-uploader__input-box {
  width: 80px !important;
  height: 80px!important;
}
.router-view {
  transition: opacity 0.4s;
}
.router-view.fade-enter,
.router-view.fade-leave-to {
  opacity: 0;
}
.common-time {
  position: absolute;
  right: 10px;
}
.common-time b {
  color: #409EFF;
}
.weui-uploader__input {
  opacity: 0 !important;
}
.picker-item.picker-selected {
  font-size: 28px;
}
