.router-view-fade.fade-enter-active {
  -webkit-transition: opacity 0.4s;
  transition: opacity 0.4s;
}
.router-view-fade.fade-enter,
.router-view-fade.fade-leave-to {
  opacity: 0;
}
.line-bottom {
  position: relative;
}
.line-bottom:after {
  content: " ";
  position: absolute;
  right: 0;
  left: 0;
  bottom: 0;
  height: 1px;
  border-bottom: 1px solid;
  -webkit-transform-origin: 0 100%;
  transform-origin: 0 100%;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
  color: #d9dbda;
}
ul li {
